import React from "react";


export default function TermosDeUso() {
  return (
<div>
    <p>
    Eles esto presentes em aplicativos, softwares e websites, mas poucos sabem ainda sobre sua real funcionalidade. Afinal, o que so e para que servem os termos de uso?



Esses textos, usualmente negligenciados, tm uma funo primordial: estabelecer normas de utilizao da plataforma pelo usurio/cliente e delinear os limites da responsabilidade do proprietrio do website ou aplicativo.



Como se v, esse documento costumeiramente visto como um aborrecimento, do qual rapidamente se foge com um clique em aceito, , na realidade, do interesse de quem quer que se coloque frente a quaisquer dos lados da tela.



Para o cliente ou usurio, eles servem como meio de inform-lo das regras de utilizao da plataforma; para o dono do website ou da aplicao, servem para limitar sua responsabilidade, habitualmente ampla, em termos legais.



O cumprimento dessas funes dos termos de uso, entretanto, exige uma redao pontual, atualizada e ajustada s particularidades de cada plataforma. Nesse sentido, duas empresas que atuem no e-commerce, por exemplo, podero ter em seus respectivos websites, termos de uso abordando questes distintas uns dos outros, muito embora sua atuao se d no mesmo ramo de atividades.



Em que pese essa especificidade e distintividade do termos,se destacam alguns elementos comuns a todos eles, dentre os quais, so os principais:



a) Descrio da atividade desenvolvida

Qual a finalidade da plataforma ou do website? Qual  o produto/servio oferecido? De que forma ele  oferecido?



A resposta a esses questionamentos formula o escopo descritivo do que est sendo oferecido ao usurio/cliente, dando a ele completa cincia daquilo com o qual est interagindo.



b) Declarao de direitos autorais e propriedade industrial

Nesse aspecto,  imprescindvel pontuar claramente a quem pertencem os direitos autorais e a propriedade industrial de cada um dos elementos auditivos, visuais, textuais, funcionais e afins, bem como os produtos ou servios, com os quais o usurio/cliente interagir.



Ele dever ser cientificado da proteo legal da qual cada um desses elementos goza, das regras para sua utilizao e reproduo e as possveis implicaes legais no caso de inobservncia dessas normas.



c) Delimitao das obrigaes e responsabilidades

Cada interao do usurio na plataforma gera obrigaes e responsabilidades tanto para ele quanto para a empresa em si e, por isso, devero ser minuciosamente delimitadas aqui.



Advertncia:  fundamental que essas obrigaes e responsabilidades estejam estritamente alinhadas com a legislao. A divergncia nesse aspecto poder ensejar nulidade dos termos e responsabilizao da empresa. 



d) Direcionamento  Poltica de Privacidade

A Poltica de Privacidade visa esclarecer como ser realizado o tratamento dos dados fornecidos pelo usurio quando na sua interao com a plataforma. Ela  legalmente indispensvel e no pode ser dissociada dos termos de uso.



Desse modo, a redao desses termos dever conter o direcionamento do usurio  pgina na qual ele poder ler e aceitar, tambm, a Poltica de Privacidade.



e) Glossrio

Textos que tratam de tecnologia e de servios e produtos de aplicaes e websites, normalmente, envolvem termos tcnicos de difcil compreenso para a maior parte da populao.



Os termos de uso so acessveis a todos e devem ser, portanto, de fcil entendimento. Dessa forma, a criao de um glossrio que proporcione essa facilitao no compreendimento do usurio,  medida necessria de ser adotada.



Esses so alguns elementos gerais, dos tantos especficos, que compem os Termos de Uso.



Para sua formulao, modelos podem ser encontrados na internet, assim como termos prontos de outros websites e aplicaes.  necessrio, contudo, ter sempre em mente que sua redao dever estar em sintonia com a legislao vigente e ser especfica para os servios/produtos oferecidos.



Na elaborao dos seus termos de uso, portanto, procure sempre um advogado especializado para lhe auxiliar.
    </p>
</div>
  
  );
}
