/* eslint-disable eqeqeq */
import React, { useState, useEffect, useCallback } from "react";
import Base from "../../Components/Base/Base";
import api from "../../services/api";
import { Table, Input, message, Tabs, Statistic } from "antd";
import {
  CrownOutlined,
  DeleteOutlined,
  CheckSquareTwoTone,
  CloseSquareTwoTone,
} from "@ant-design/icons";
import { useSession } from "../../Context/SessionContext";
import ActionButton from "../../Components/ActionButton/actionButton";

import "./ListaAlunos.css";
import { BsPeople } from "react-icons/bs";
import handleError from "../../utils/handleError";

export default function ListaAlunos() {
  const [students, setStudents] = useState([]);
  const [pending, setPending] = useState([]);

  const [search, setSearch] = useState("");
  const [filtered, setFiltered] = useState([]);
  const [loading, setLoading] = useState(true);

  const { session } = useSession();
  const { TabPane } = Tabs;

  const type = session.user.type;

  const config = {
    headers: {
      authorization: "BEARER " + session.accessToken,
    },
  };

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const getData = useCallback((tab) => {
    if (session.user.type !== "master")
      config.params = { "user.organization_id": session.user.organization_id };
    setLoading(true);
    api
      .get("/user", config)
      .then((res) => {
        setStudents(
          res.data
            .filter((user) => user.status === "approved")
            .sort((a, b) => b.score - a.score)
        );
        setPending(res.data.filter((user) => user.status === "pending"));
        setFiltered(
          res.data
            .filter(
              (user) => user.status === (tab === 0 ? "approved" : "pending")
            )
            .sort((a, b) => b.score - a.score)
        );
      })
      .finally(() => setLoading(false));
  });

  useEffect(() => {
    getData(0);
    setFiltered(students);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const columns = [
    {
      title: "Nome",
      dataIndex: "name",
      width: "25%",
    },
    {
      title: "Matrcula",
      dataIndex: "registration",
      className: type === "student" ? "hide" : "",
      align: "left",
      width: "20%",
      render: (registration) =>
        type === "student" ? null : <p>{registration}</p>,
    },
    {
      title: "Organizao",
      className: type === "master" ? "column-organization" : "hide",
      dataIndex: "organization_name",
      width: "20%",
      render: (registration) =>
        type === "master" ? <p>{registration}</p> : null,
    },
    {
      title: "Ocupao",
      className: type === "student" ? "hide" : "column-turma",
      dataIndex: "occupation_name",
      width: "25%",
      render: (registration) =>
        type === "student" ? null : <p>{registration}</p>,
    },
  ];

  function isAdmin(id) {
    const user = filtered.filter((x) => x.id == id);
    if (user[0].type == "admin" || user[0].type == "master") {
      return (
        <ActionButton
          title="Rebaixar"
          confirm="Rebaixar para estudante?"
          onConfirm={() => handleDemote(id)}
        >
          <CrownOutlined
            className="actionButton"
            style={{ color: "#f0a500" }}
          />
        </ActionButton>
      );
    } else {
      return (
        <ActionButton
          title="Promover"
          confirm="Promover para admin?"
          onConfirm={() => handlePromote(id)}
        >
          <CrownOutlined className="actionButton" />
        </ActionButton>
      );
    }
  }

  const studentsTable = [
    {
      title: "Posio",
      dataIndex: "id",
      width: "10%",
      render: (id) => <p>{findPositionById(id)}</p>,
    },
    ...columns,
    {
      title: "Pontuao",
      dataIndex: "score",
      render: (score) => <p>{score * 20} XP</p>,
    },
    {
      title: "Aes",
      dataIndex: "id",
      className: type === "master" ? "" : "hide",
      render: (id) => {
        return type === "master" ? (
          <>
            {isAdmin(id)}
            {/* <ActionButton title="Editar" confirm="Editar turma?">
              <EditOutlined className="actionButton" />
            </ActionButton> */}
            <ActionButton
              title="Deletar"
              confirm="Deletar usurio?"
              onConfirm={() => handleDelete(id)}
            >
              <DeleteOutlined className="actionButton" />
            </ActionButton>
          </>
        ) : null;
      },
    },
  ];

  const pendingTable = [
    ...columns,
    {
      title: "Aprovar",
      dataIndex: "id",
      render: (id) => {
        return (
          <>
            <ActionButton
              title="Aprovar"
              confirm="Aprovar usurio?"
              onConfirm={() => handleApprove(id, "approved")}
            >
              <CheckSquareTwoTone twoToneColor="limeGreen" />
            </ActionButton>
            <ActionButton
              title="Negar"
              confirm="Negar usurio?"
              onConfirm={() => handleApprove(id, "refused")}
            >
              <CloseSquareTwoTone twoToneColor="red" />
            </ActionButton>
          </>
        );
      },
    },
  ];

  function handleTabChange(key) {
    setFiltered(key === "0" ? students : pending);
  }

  function handlePromote(id) {
    api
      .put(`/user/`, { id, type: "admin" }, config)
      .then(() => {
        getData(0);
        message.success(`O usurio agora  admin`);
      })
      .catch((err) =>
        handleError(err, "no foi possvel tornar usurio admin")
      );
  }

  function handleDemote(id) {
    api
      .put(`/user`, { id, type: "student" }, config)
      .then(() => {
        message.success(`O usurio agora  estudante`);
        getData(0);
      })
      .catch((err) =>
        handleError(err, "no foi possvel tornar usurio estudante")
      );
  }

  function handleApprove(id, status) {
    setLoading(true);

    api
      .put(`/user`, { id, status: status }, config)
      .then(() => {
        if (status === "approved") message.success(`Usurio aprovado!`);
        if (status === "refused") message.success(`Usurio negado!`);
        getData(1);
      })
      .catch((err) => {
        handleError(err, "No foi possvel alterar o status do usurio!");
      });
  }

  function handleDelete(id) {
    api
      .put(`/user/${id}`, {}, config)
      .then(() => {
        message.success(`Usurio deletado com sucesso`);
        getData(0);
      })
      .catch((err) =>
        handleError(
          err,
          "No foi possvel deletar usurio. Tente novamente mais tarde"
        )
      );
  }

  function handleSearch(value, key) {
    setSearch(value);
    const source = [students, pending];

    setFiltered(
      source[key].filter((student) => {
        if (value === "") return student;
        return (
          student.name.toLowerCase().includes(value.toLowerCase()) ||
          student.registration.toString().includes(value.toLowerCase()) ||
          student.organization_name
            .toLowerCase()
            .includes(value.toLowerCase()) ||
          student.occupation_name.toLowerCase().includes(value.toLowerCase())
        );
      })
    );
  }

  function findPositionById(id) {
    let position = -1;
    students.forEach((element, index) => {
      if (element.id === id) position = index + 1;
    });
    return position;
  }
  return (
    <Base>
      <h1 className="page-title">
        {session.user.type === "student" ? "Ranking" : "Lista de Alunos"}
      </h1>
      <div className="table-container">
        {session.user.type === "student" && (
          <h5 className="greyish">
            <Statistic
              title="Minha posio"
              value={findPositionById(session.user.id) + ""}
              prefix={<BsPeople />}
            />
          </h5>
        )}
        <Tabs defaultActiveKey="0" onChange={handleTabChange}>
          <TabPane tab="Alunos" key="0">
            <Input
              className="search-input"
              placeholder="Pesquisar..."
              onChange={(e) => handleSearch(e.target.value, 0)}
              value={search}
            />
            <Table
              columns={studentsTable}
              dataSource={filtered}
              loading={loading}
            />
          </TabPane>
          {type === "master" ? (
            <TabPane tab="Pendentes" key="1">
              <Input
                className="search-input"
                placeholder="Pesquisar..."
                onChange={(e) => handleSearch(e.target.value, 1)}
                value={search}
              />
              <Table
                columns={pendingTable}
                dataSource={filtered}
                loading={loading}
              />
            </TabPane>
          ) : (
            <TabPane tab="Pendentes" key="1">
              <Input
                className="search-input"
                placeholder="Pesquisar..."
                onChange={(e) => handleSearch(e.target.value, 1)}
                value={search}
              />
              <Table
                columns={pendingTable}
                dataSource={filtered}
                loading={loading}
              />
            </TabPane>
          )}
        </Tabs>
      </div>
    </Base>
  );
}
