import { message } from "antd";

const defaultErrors = {
  400: "Erro de validao",
  401: "No autorizado",
  404: "No encontrado",
  403: "Acesso negado",
  408: "Tempo limite excedido",
  409: "Conflito",
  422: "Erro de validao",
  429: "Limite de requisies excedido",
  500: "Erro interno do servidor",
  502: "Erro de comunicao",
  503: "Servio indisponvel",
  504: "Tempo limite excedido",
  513: "Escolha uma imagem de menor resoluo",
};

export default function handleError(error, messageContent) {
  const { response } = error;
  if (response && response.status > 299) {
    const { status } = response;
    const messageError =
      response.data.message || messageContent || defaultErrors[status];
    message.error(messageError);
  }
}
